* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.nav-list {
    height: 10vh;
    display: flex;
    width: 50%;
    justify-content: space-around;
    align-items: center;
    margin-left: auto;
    list-style: none;
}
.nav-links {
    text-decoration: none;
    color: black;
    font-size: 18px;
}
.home, .about, .contact, .projects, .tecs {
    height: 90vh;
    position: absolute;
    width: 100%;
}
.home-content, .about-content, .contact-content, .projects-content, .tecs-content {
    height: 100%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    img {
        height: 70%;
    }
    h1 {
        font-size: 50px;
        margin: 30px 0;
    }
    p {
        font-size: 20px;
    }
}
.home {
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}
.about {
    background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
}
.contact {
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
.projects{
    background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
}
.tecs{
    background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
}
.contenedor{
    max-width: 1280px;
    margin: auto;
}
.about-presentation h1 p {
    width: 50%;
}